<template>
<div>
    <div class="row">
        <div class="col-12">
            <div class="page-title-box d-flex align-items-center justify-content-between">
                <h4 class="mb-0 font-size-18">Cambios Atuais</h4>
                <div class="page-title-right">
                    <a href="#modal-editar" @click="abreModalAtualizarCambio()" class="btn green-jungle btn-md" data-toggle="modal">
                        <i class="fa fa-plus-circle"></i>
                        <span class="hidden-xs">Atualizar Cambio</span>
                    </a>
                </div>

            </div>
        </div>
    </div>
    <!-- end page title -->
    <div class="row" style="padding:10px">
        <!-- <div class="col-sm-12 col-md-3">                  
            <label >Mostrar  
            <select class="custom-select custom-select-sm form-control form-control-sm" v-model="filtro.per_page" @input="debounceBusca">
                <option :value="10">10</option>
                <option :value="25">25</option>
                <option :value="50">50</option>
                <option :value="100">100</option>
            </select>
            </label>                  
        </div> -->
        <div class="col-sm-12 col-md-7">
            <label style="width:90%">Buscar por:
            <input type="text" class="form-control form-control-sm" placeholder="Nome / Descrição" v-model="filtro.texto" @input="debounceBusca" >
            </label>                  
        </div>
        <div class="col-sm-12 col-md-2">
            <label style="width:90%">Filtrar:
            <button @click="navegar(1)" class="btn btn-outline-primary btn-sm btn-block"><span class="bx bx-search-alt"></span></button>
            </label>
        </div>
    </div>
    <div class="row">
        <div class="col-12">
            <div class="card">
                <div class="card-body">
                    <h4 class="card-title">Lista de Taxas</h4>
                    <div class="table-responsive">
                            <table class="table mb-0" >
                            <thead>
                            <tr>
                                <th> Id </th>
                                <th class="text-right"> Moeda  </th>
                                <th></th>
                                <th class="text-center"> Moeda  </th>
                                <th class="text-left"> Taxa </th>
                            </tr>
                            </thead>
                            <tbody>
                                <tr v-for="registro in cambios" :key="registro.id">
                                    <td>{{registro.id}}</td>
                                    <td class="text-right">{{registro.moeda_origem.nome}} </td>
                                    <td class="text-center">{{ registro.multiplicar ? 'x' : '/'}}</td>
                                    <td class="text-left">{{registro.moeda_destino.nome}} </td>
                                    <td class="text-right">{{ registro.ultima_taxa.taxa | valor }}</td>
                                </tr>
                            
                            </tbody>
                            <!-- <tfoot>
                                <tr>
                                    <td colspan="4"> 
                                        <paginacao-componente :source="paginacao" @navigate="navegar($event)"></paginacao-componente>
                                    </td>
                                </tr>
                            </tfoot> -->
                        </table>
                    </div>

                </div>
            </div>
        </div> <!-- end col -->
    </div> <!-- end row -->

    <div class="modal fade" id="modal-editar" tabIndex="1">
        <div class="modal-dialog modal-lg " role="document">
            <form class="modal-content" @submit.prevent="atualizarCambio()">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal">×</button>
                    <h4 class="modal-title">Atualização de taxas de Cambio</h4>
                </div>
                <div class="modal-body" >
                    <div class="row">
                         <div class="col-md-4">
                            <label class="control-label">Moedas</label>
                        </div>
                        <div class="col-md-3">
                            <label class="control-label">Taxa Atual</label>
                        </div>
                        <div class="col-md-3">
                            <label class="control-label">Nova Taxa</label>
                        </div>
                        <div class="col-md-2">
                            <label class="control-label">Variação</label>
                        </div>

                    </div>
                    <div class="row" v-for="taxa in cambioModal.taxas" :key="taxa.id">

                        <div class="col-md-4">
                            <input class="form-control" :disabled="true" :value="`${taxa.moeda_origem.nome} - ${taxa.moeda_destino.nome}`" />
                        </div>
                        <div class="col-md-3">
                            <money class="form-control" :disabled="true" :value="taxa.ultima_taxa.taxa" v-bind="money" ></money>
                        </div>
                        <div class="col-md-3">
                            <money class="form-control" v-model="taxa.taxa" v-bind="money" @input="calcularVariacao(taxa)"></money>
                        </div>
                        <div class="col-md-2">
                            
                          <i v-if="taxa.variacao > 0" class="fa fa-arrow-up" :class="{'red': taxa.variacao > 10 || taxa.variacao < -10}" ></i>
                          <i v-if="taxa.variacao < 0" class="fa fa-arrow-down" :class="{'red': taxa.variacao > 10 || taxa.variacao < -10}" ></i>
                          <i v-if="taxa.variacao == 0" class="fa fa-minus"  ></i>
                          {{taxa.variacao | valor}}
                        </div>                        

                    </div>
                      <div class="row" v-if="variacao_alta" >
                        <div class="col-md-12">
                            <hr>
                           <div class="alert alert-danger text-center">Existem taxas com variação maior que 10%<br> <strong>Tem certeza que deseja atualizar com essas taxas?</strong></div>
                            <div class="form-control text-right">
                                <label for="confirmar_taxas" class="control-label">Confirmar atualização</label>
                                <input type="checkbox" name="confirmar_taxas" v-model="variacao_confirmada" id="confirmar_taxas" class="">
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12" v-if="erroEditar || sucessoEditar">
                            <hr>
                            <mensagem-componente :erros="erroEditar" @fechar="erroEditar=null"></mensagem-componente>
                            <mensagem-componente :sucesso="sucessoEditar" @fechar="sucessoEditar=null"></mensagem-componente>
                        </div>
                    </div>
                </div>
                 
                <div class="modal-footer">
                    <button type="button" class="btn btn-default" data-dismiss="modal">Fechar</button>
                    <button type="submit" class="btn btn-primary" :disabled="(variacao_alta && !variacao_confirmada)">
                        <i class="fa fa-save"></i> Atualizar
                    </button>
                </div>  
            </form>
        </div>
    </div>


</div>

    
</template>

<script>
import { CambioService } from '../../../services'
import { required, minValue } from "vuelidate/lib/validators";
const cambioService = new CambioService();
export default {
   
    data(){
        return{
            cambios:[],
            filtro:{
                ordenar: 'id',
                ordem: 'asc',
                page: 1,
                per_page: 10000,
                texto: '',
            },
                       
            sucessoEditar: null,
            erroEditar: null,
            cambioModal: {
                taxas: []
            },
            variacao_alta: false,
            variacao_confirmada: false,
            
            money: {
                decimal: ',',
                thousands: '.',
                prefix: '',
                suffix: '',
                precision: 2,
                masked: false
            }
        }   
    },
    validations() {
        return {
            cambioModal: {
                taxas: {
                    required,
                    $each:{
                        id: required,
                        taxa: {required, minValue: minValue(0)}
                    }
                }
            },
        }
    },
    methods:{
        debounceBusca: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.navegar(1);
            }, 800);
        },
        
        buscarCambios:function(){
            cambioService.getCambioAgora(this.filtro).then(
                response => {
                    this.cambios = response.data.map(x => {
                        x.ultima_taxa.taxa = x.ultima_taxa ? Number(x.ultima_taxa.taxa) : 0;
                        return x;
                    });
                }
            )
        },
        abreModalAtualizarCambio: function(){
            this.erroEditar = null;
            this.sucessoEditar = null;
            this.$v.cambioModal.$reset();
            
            this.cambioModal.taxas = [...this.cambios.map(x => { 
                x = {...x};
                x.taxa = x.ultima_taxa ? x.ultima_taxa.taxa : 0; 
                x.variacao = 0;
                return x; 
            })];
            this.variacao_alta = false;
            this.variacao_confirmada= false;
        },
        calcularVariacao: function(taxa){
            if(taxa.taxa <= 0){
                taxa.variacao = -100;
                return 0;
            }                    
            let variacao = (taxa.taxa * 1) - (taxa.ultima_taxa.taxa * 1);
            taxa.variacao = Math.round((variacao / taxa.ultima_taxa.taxa) * 10000) / 100;
                
        },
        atualizarCambio: function(){
            this.sucessoEditar = null;
            this.erroEditar = null;
            if(this.cambioModal.taxas.filter(x => x.taxa <= 0).length > 0){
                this.erroEditar = "Existem taxas com valores zerados, ou negativos. Corrija para prosseguir!";
                return 0;
            }
            
            if((this.cambioModal.taxas.filter(x => x.variacao > 10 || x.variacao < -10).length > 0) && !this.variacao_confirmada ){
                this.variacao_alta = true;
                return 0;
            }
            
            let req = cambioService.atualizarCambios(this.cambioModal.taxas)
            req.then( response => {
                
                this.cambios = response.data.map(x => {
                    x.ultima_taxa.taxa = Number(x.ultima_taxa.taxa);
                    return x;
                })
                this.abreModalAtualizarCambio();
                
                this.sucessoEditar = 'Taxas atualizadas com sucesso!';
                setTimeout(() => {
                    location.reload()
                }, 2000);
            }).catch( erro => this.erroEditar = erro)    
        }
       
    },
    
    mounted(){
        this.buscarCambios();
    },
}
</script>